:root {
  /* Dark Theme */
  --tg-theme-bg-color-dark: #15191E;
  --tg-theme-text-color-dark: #A7C3FF;
  --tg-theme-hint-color-dark: #CCCCCC;
  --tg-theme-link-color-dark: #4C8BF5;
  --tg-theme-button-color-dark: #0088CC;
  --tg-theme-button-text-color-dark: #FFFFFF;
  --tg-theme-secondary-bg-color-dark: #1C1F23;
  --tg-theme-header-bg-color-dark: #FFFFFF;
  --tg-theme-accent-text-color-dark: #FFFFFF;
  --tg-theme-section-bg-color-dark: #1C1F23;
  --tg-theme-section-header-text-color-dark: #FFFFFF;
  --tg-theme-subtitle-text-color-dark: #CCCCCC;
  --tg-theme-destructive-text-color-dark: #FF0000;

  /* Light Theme */
  --tg-theme-bg-color-light: #F7F8FA;
  --tg-theme-text-color-light: #1D2025;
  --tg-theme-hint-color-light: #CCCCCC;
  --tg-theme-link-color-light: #4C8BF5;
  --tg-theme-button-color-light: #0088CC;
  --tg-theme-button-text-color-light: #FFFFFF;
  --tg-theme-secondary-bg-color-light: #FFFFFF;
  --tg-theme-header-bg-color-light: #1C1F23;
  --tg-theme-accent-text-color-light: #1D2025;
  --tg-theme-section-bg-color-light: #FFFFFF;
  --tg-theme-section-header-text-color-light: #1D2025;
  --tg-theme-subtitle-text-color-light: #CCCCCC;
  --tg-theme-destructive-text-color-light: #FF0000;
}

/* Use CSS variables based on the user's theme */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Set background and text color based on the theme */
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}





